.wp-block-ncmfse-toggle-dark-mode {
	display: inline-block;
	position: relative;
	cursor: pointer;
	&:focus {
		outline: none;
	}
}

.wp-block-ncmfse-toggle-dark-mode__icon {
	display: flex;
	&:not(.activated) {
		visibility: hidden;
		opacity: 0;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
	}
	svg {
		color: currentColor;
		width: 1.5em;
		height: 1.5em;
	}
}

