// ----------------------------------------
.dark,
.dark * {
	.wp-block-outermost-social-sharing.is-style-logos-only {
		.outermost-social-sharing-link-x,
		.outermost-social-sharing-link-threads,
		.outermost-social-sharing-link-tumblr {
			color: var(--wp--preset--color--shadcn-foreground);
		}
	}
}

:where(.dark, .dark *) :where(.wp-block-social-links.is-style-logos-only) {
	.wp-social-link-x,
	.wp-social-link-threads,
	.wp-social-link-tiktok,
	.wp-social-link-tumblr,
	.wp-social-link-patreon,
	.wp-social-link-medium,
	.wp-social-link-fivehundredpx {
		color: var(--wp--preset--color--shadcn-foreground);
	}

	.wp-social-link-github,
	.wp-social-link-goodreads,
	.wp-social-link-codepen {
		color: var(--wp--preset--color--contrast-2);
	}
}

